.social-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;

  list-style: none;

  &__item {
    margin-right: 59px;

    @include vp-1279 {
      margin-right: 30px;
    }
  }

  &__item:last-child {
    margin-right: 0;
  }

  &__link {
    display: flex;
    border-radius: 50px;
    outline: none;
    transform: translateZ(0);

    transition: opacity $trans-ease-in-out;

    @include hover {
      opacity: 0.5;
    }

    @include active {
      opacity: 1;
    }

    @include focus {
      outline: 4px solid $color-finlandia-opacity-50;
      outline-offset: 3px;
    }
  }

  svg {
    transform: translateZ(0);
  }
}
