.filter-reason {
  padding-bottom: 27px;

  &__title {
    position: relative;
    padding-top: 21px;
    padding-left: 8px;
    margin: 0 0 132px 0;
    font-family: $ff-roboto;
    font-weight: 400;
    letter-spacing: 0.1em;
    color: $color-default-black;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 8px;
      display: block;
      width: 540px;
      height: 1px;
      background-color: $color-default-black;
    }
  }

  &__form-fields {
    display: flex;
    flex-wrap: wrap;

    &:has(:focus-visible) {
      outline: 4px solid $color-finlandia-opacity-50;
      outline-offset: 13px;
    }
  }

  &__form-field {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    margin-right: 40px;
    margin-bottom: 110px;
    max-width: 620px;
    min-height: 45px;

    &:last-child {
      margin-right: 0;
    }

    &--for-all {
      margin-left: 17px;
      min-width: 170px;
    }

    &--for-birthday {
      min-width: 305px;
    }

    &--for-bride {
      min-width: 190px;
    }

    &--for-mother {
      min-width: 210px;
    }

    &--for-colleague {
      min-width: 214px;
    }

    &--for-darling {
      flex-grow: 1;

      @include vp-1699 {
        flex-grow: 0;
        margin-left: 30px;
      }
    }

    @include vp-1023 {
      margin-right: 30px;
      margin-bottom: 20px;
    }
  }
}
