.btn-close {
  position: relative;
  width: 55px;
  height: 55px;

  border: 0;
  background-color: transparent;
  cursor: pointer;

  transition: opacity $trans-ease-in-out;

  &::after {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;

    border-radius: 5px;

    content: "";
    z-index: -1;
  }

  & svg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  @include hover-focus {
    opacity: 0.6;
  }

  @include focus {
    outline: none;

    &::after {
      outline: 4px solid $color-finlandia-opacity-50;
      outline-offset: 5px;
    }
  }

  &--dark {
    width: 72px;
    height: 74px;
    padding: 10px 8px;

    background-color: $color-finlandia;
    border-radius: 20px;
    color: $color-default-white;

    overflow: hidden;

    transition: background-color $trans-ease-in-out;

    &::before {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;

      background-color: $color-default-white;

      border-radius: 20px;

      content: "";
      z-index: -1;
    }

    & svg {
      position: relative;
      width: 100%;
      height: 100%;
    }

    @include hover-focus {
      opacity: 1;
      background-color: $color-finlandia-opacity-50;
    }

    @include focus {
      outline: 4px solid $color-finlandia-opacity-50;
      outline-offset: 5px;
    }
  }
}
