.button-heart {
  display: inline-block;
  padding: 0;
  width: 75px;
  height: 75px;
  cursor: pointer;
  user-select: none;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  z-index: 3;

  svg {
    pointer-events: none;
  }

  @include hover-focus {
    & .button-heart__stroke {
      fill: $color-default-black-opacity-60;
    }


    @include active {
      & .button-heart__body {

        transform: scale(1.02);
        transform-origin: center;
        fill: $color-default-black;
      }
    }
  }

  &:focus-visible {
    outline: 4px solid $color-finlandia-opacity-50;
    outline-offset: 3px;
  }

  &__stroke {
    transition: opacity $trans-default;
    fill: $color-default-black;
  }

  &__body {
    transition: opacity $trans-default;
  }

  &:disabled {
    pointer-events: none;

    & .button-heart__stroke,
    & .button-heart__body {
      opacity: 0.2;
    }
  }
}
