.message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 1080px;
  min-height: 664px;
  padding: 90px 210px;

  font-size: 64px;
  line-height: 75px;

  background-color: $color-floral-white;
  border-radius: 60px;

  @include vp-1439 {
    padding: 70px 150px;
    max-width: 900px;
    min-height: 500px;

    font-size: 54px;
    line-height: 63px;

    border-radius: 40px;
  }

  @include vp-1279 {
    padding: 60px 100px;
    max-width: 750px;
    min-height: 400px;

    font-size: 46px;
    line-height: 54px;
  }

  &--titled {
    padding: 90px 75px;
    justify-content: flex-start;

    @include vp-1439 {
      padding: 70px 50px;
    }

    @include vp-1279 {
      padding: 60px 40px;
    }
  }
}

.message__title {
  margin: 0;
  margin-bottom: 108px;

  font-weight: 400;

  @include vp-1439 {
    margin-bottom: 80px;
  }

  @include vp-1279 {
    margin-bottom: 60px;
  }
}

.message__text {
  margin: 0;
}
