.title {
  font-weight: 400;
  color: inherit;
  letter-spacing: 0.1em;
  margin: 0;

  &--h1 {
    font-weight: 400;
    font-size: 82px;
    line-height: 96px;
    color: $color-floral-white;
  }

  &--h2 {
    font-size: 54px;
    line-height: 63px;
    letter-spacing: 0.04em;
  }

  &--h3 {
    font-size: 40px;
    line-height: 47px;
  }

  &--h4 {
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.04em;
  }

  &--header {
    font-weight: 400;
    font-size: 64px;
    line-height: 75px;
    color: $color-default-black;
    text-transform: uppercase;
    letter-spacing: 10px;
  }
}
