.product-description {
  display: flex;
  flex-direction: column;
  padding: 35px 160px 0 191px;

  @include vp-1439 {
    padding: 25px 100px 0 130px;
  }

  @include vp-1279 {
    padding: 20px 40px 0 60px;
  }
}

.product-description__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 100px;

  @include vp-1279 {
    margin-bottom: 60px;
  }
}

.product-description .title {
  margin: 0;
  margin-right: 50px;

  @include vp-1279 {
    margin-right: 30px;

    font-size: 51px;
    line-height: 59px;
  }
}

.product-description .price {
  @include vp-1279 {
    font-size: 48px;
    line-height: 56px;
  }
}

.product-description .text {
  margin: 0;
  margin-bottom: 30px;
  max-width: 930px;

  &:last-of-type {
    margin-bottom: 95px;
  }

  @include vp-1279 {
    margin-bottom: 20px;

    font-size: 36px;
    line-height: 45px;

    &:last-of-type {
      margin-bottom: 60px;
    }
  }
}

.product-description__button {
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 0;
  }
}
