.filter-field-img {
  &__input {
    position: absolute;
    padding: 0;
    margin: -1px;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    white-space: nowrap;
    border: 0;
    clip-path: inset(100%);

    &:checked + .filter-field-img__label {
      .filter-field-img__text {
        font-weight: 600;
        color: $color-finlandia;
        transform: scale(1.4) translateY(10px);

        &::before {
          content: "";
          position: absolute;
          right: 0;
          bottom: -7px;
          left: 0;
          display: block;
          height: 4px;
          transition: opacity $trans-default;
          background-color: $color-finlandia;
        }
      }

      .filter-field-img__img {
        img {
          transform: scale(1.19);
        }
      }
    }

    &:hover:not(:checked) + .filter-field-img__label {
      .filter-field-img__text {
        font-weight: 500;
        transform: scale(1.2);
      }
    }

    &:disabled + .filter-field-img__label {
      pointer-events: none;

      img {
        opacity: 0.5;
      }

      .filter-field-img__text {
        color: $color-default-black-opacity-50;
      }
    }


    &:focus-visible + .filter-field-img__label {
      &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: -25px;
        left: 0;
        outline: 4px solid $color-finlandia-opacity-50;
        outline-offset: 10px;
      }
    }
  }

  &__label {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    cursor: pointer;
  }

  &__text {
    position: relative;
    font-family: $ff-roboto;
    font-size: 20px;
    font-weight: 300;
    line-height: 23px;
    letter-spacing: 0.15em;
    color: $color-default-black;
    transition: transform $trans-default, color $trans-default;
    transform-origin: top;
    text-align: center;
    will-change: transform;
  }

  &__img {
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 155px;
    height: 155px;

    img {
      width: 130px;
      height: 130px;
      overflow: hidden;
      transition: transform $trans-default;
      border-radius: 50%;
      will-change: transform;
    }
  }
}
