.btn-calculate {
  font-weight: 500;
  font-size: 48px;
  line-height: 56px;
  color: $color-default-black;

  background-color: $color-default-white;
  border: none;
  border-radius: 20px;
  outline: none;
  cursor: pointer;

  transition: opacity $trans-ease-in-out;

  padding: 0;

  user-select: none;

  @include hover-focus {
    opacity: 0.6;
  }

  @include focus {
    outline: 4px solid $color-finlandia-opacity-50;
    outline-offset: 1px;
  }
}
