.header {
  padding: 74px 0;

  background-color: $color-default-white;

  .container {
    padding: 0 90px 0 114px;
  }

  &__wrapper {
    display: grid;

    grid-template-columns: 20% 1fr 20%;
  }

  .logo {
    padding-left: 5px;
    align-self: center;
  }

  &__title {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .title {
    max-width: 710px;
    text-align: center;
  }

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    justify-self: flex-end;
  }
}
