.mission {
  margin-bottom: 10px;

  background: transparent;

  &__wrapper {
    padding-top: 28px;
    padding-left: 10px;
  }

  h2 {
    position: relative;

    margin: 0 0 180px 0;

    @media (max-width: 1600px) {
      margin-bottom: 100px;
    }

    &::after {
      content: "";
      position: absolute;
      top: -28px;
      left: 0;

      width: 540px;
      height: 1px;

      background-color: $color-default-black;
    }
  }

  &__content {
    position: relative;

    padding-left: 150px;
  }

  &__quote {
    position: relative;

    display: flex;
    flex-direction: column;
    padding: 100px 191px 107px 332px;

    background-color: $color-default-white;

    @media (max-width: 1620px) {
      padding-top: 40px;
      padding-right: 100px;
    }

    @include vp-1279 {
      padding-right: 50px;
      padding-left: 240px;
    }
  }

  .text {
    position: relative;

    align-self: center;
    max-width: 865px;

    @include vp-1279 {
      font-size: 30px;
    }

    &::before {
      content: "\201C";
      position: absolute;
      top: 1px;
      left: -31px;

      font-weight: 700;
      font-size: 106px;
      font-family: "Roboto", "Arial", sans-serif;
    }

    &::after {
      content: "\201E";
      position: absolute;
      right: -55px;
      bottom: 13px;

      font-weight: 700;
      font-size: 106px;
      font-family: "Roboto", "Arial", sans-serif;
    }
  }

  svg {
    position: absolute;
  }

  &__icon-left {
    top: 34px;
    left: 318px;

    @include vp-1279 {
      left: 100px;
    }

    @media (max-width: 1620px) {
      top: -20px;
      left: 245px;
    }
  }

  &__icon-right {
    right: 154px;
    bottom: 50px;

    @include vp-1279 {
      right: 70px;
    }

    @media (max-width: 1620px) {
      right: 60px;
    }
  }

  &__img {
    position: absolute;
    top: -190px;
    left: -108px;

    @include vp-1279 {
      left: -48px;
    }

    @media (max-width: 1620px) {
      top: -120px;

      padding-top: 60px;
    }
  }

  img {
    width: 623px;
    height: 623px;

    @include vp-1279 {
      width: 350px;
      height: 350px;
    }

    @media (max-width: 1620px) {
      width: 500px;
      height: 500px;
    }
  }
}
