.price {
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
  white-space: nowrap;

  & span {
    margin-left: 8px;

    font-size: 16px;
    font-weight: 300;
    line-height: 19px;
  }

  &--size-middle {
    font-size: 48px;
    font-weight: 500;
    line-height: 56px;

    & span {
      font-size: 32px;
      font-weight: 300;
      line-height: 38px;
    }
  }

  &--size-big {
    font-size: 64px;
    font-weight: 500;
    line-height: 75px;

    & span {
      font-size: 32px;
      line-height: 38px;
    }
  }

  &--size-middle-p {
    font-size: 48px;
    font-weight: 500;
    line-height: 56px;

    & span {
      font-size: 32px;
      font-weight: 300;
      line-height: 38px;
      text-transform: lowercase;
    }
  }

  &--size-min {
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;

    & span {
      font-size: 14px;
      font-weight: 300;
      line-height: 17px;
      text-transform: lowercase;
    }
  }
}
