.logo {
  &__link {
    transition: opacity $trans-ease-in-out;
    outline: none;

    @include hover {
      opacity: 0.5;
    }

    @include active {
      opacity: 1;
    }

    @include focus {
      svg {
        outline: 4px solid $color-finlandia-opacity-50;
        outline-offset: 3px;
      }
    }
  }
  
  svg {
    transform: translateZ(0);
  }
}
