.text {
  font-weight: 300;
  letter-spacing: 0.04em;
  color: inherit;
  margin: 0;

  &--size-20 {
    font-size: 20px;
    line-height: 23px;
  }

  &--size-20-30 {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 3px;
  }

  &--size-40 {
    font-size: 40px;
    line-height: 47px;
  }

  &--size-64 {
    font-size: 64px;
    line-height: 75px;
  }

  &--align-center {
    text-align: center;
  }

  &--quote {
    font-style: italic;
    font-size: 38px;
    line-height: 55px;
    text-align: center;
    letter-spacing: 3.5px;
  }

  &--total {
    font-weight: 400;
    font-size: 48px;
    line-height: 56px;
    color: $color-default-black;
    letter-spacing: 12px;
  }
}
