.hero {
  margin-bottom: 110px;

  &__wrapper {
    position: relative;

    display: flex;
    flex-direction: column;
  }

  &__background {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: $color-finlandia;

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  &__content {
    z-index: 10;

    max-width: 52.5%;
    padding: 70px 50px 40px 116px;

    background-color: $color-default-black-opacity-20;

    @include vp-1439 {
      max-width: 60%;
      padding-left: 70px;
    }
  }

  .title {
    margin-bottom: 93px;
  }

  .btn {
    margin-right: 10px;
    margin-bottom: 10px;
  }

  &--popup {
    margin-bottom: 0;

    .hero__content {
      padding: 252px 54px 128px;
      max-width: 51.5%;

      @media (max-width: 1540px) {
        padding: 150px 20px 120px;
      }

      @include vp-1279 {
        padding: 70px 20px;
      }
    }

    .btn {
      margin: 0;
    }

    .hero__popupclose {
      position: absolute;
      top: 10px;
      right: 17px;
    }

    .hero__loader {
      display: none;
      position: absolute;
      top: 10px;
      right: 17px;
      color: $color-default-white;
    }

    .hero__loader-icon {
      animation-name: rotation;
      animation-duration: 3s;
      animation-timing-function: linear;
      animation-iteration-count: infinite;

      @keyframes rotation {
        0% {
          transform: rotate(0deg);
        }

        100% {
          transform: rotate(360deg);
        }
      }
    }

    .title {
      letter-spacing: 0.06em;
      line-height: 86px;
      margin-bottom: 93px;

      @include vp-1439 {
        font-size: 70px;
      }

      @include vp-1279 {
        font-size: 50px;
      }
    }

    &.is-loading {
      .hero__popupclose {
        display: none;
      }

      .hero__loader {
        display: flex;
      }
    }
  }
}
