.error-message {
  padding: 300px 100px 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;

  @include vp-1439 {
    padding: 200px 60px 200px;
  }

  @include vp-1279 {
    padding: 100px 40px 100px;
  }
}

.error-message .message {
  margin-bottom: 162px;

  @include vp-1439 {
    margin-bottom: 130px;
  }

  @include vp-1279 {
    margin-bottom: 100px;
  }
}

.error-message__button {
  max-width: 800px;
  margin-bottom: 30px;

  &:last-of-type {
    margin-bottom: 0;
  }

  @include vp-1439 {
    max-width: 600px;
  }

  @include vp-1279 {
    max-width: 400px;
  }
}
