.btn {
  position: relative;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  padding: 27px 20px 26px;

  font-size: 40px;
  line-height: 47px;
  color: $color-default-white;
  letter-spacing: 0.15em;
  text-align: left;

  background-color: $color-finlandia-opacity-70;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;

  transition: background-color $trans-default;
  overflow: hidden;

  @include hover-focus {
    background-color: $color-finlandia-opacity-50;
  }

  @include focus {
    outline: 4px solid $color-finlandia-opacity-50;
    outline-offset: 3px;
  }

  @include active {
    background-color: $color-finlandia;
  }

  &:disabled,
  &.is-disabled {
    background-color: $color-finlandia-opacity-20;

    pointer-events: none;
  }

  &::before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    background-color: $color-default-white;

    border-radius: 5px;

    content: "";
    z-index: -1;
  }

  &--hero {
    display: inline-flex;
    flex-direction: row;
    align-items: center;

    border: 2px solid $color-finlandia;
    background-color: $color-finlandia-opacity-70;
    transition:
      background-color $trans-default,
      border-color $trans-default;

    svg {
      margin-left: 50px;
      flex-shrink: 0;

      color: $color-default-white;
    }

    @include hover-focus {
      background-color: $color-finlandia-opacity-50;
    }

    @include active {
      border-color: $color-finlandia;
      transition: none;
    }
  }

  &--outlined {
    padding: 17px 71px 16px 73px;
    justify-content: center;

    letter-spacing: 0.1em;
    text-align: center;

    background-color: $color-finlandia-opacity-70;
    transition: background-color $trans-default;

    @include hover-focus {
      background-color: $color-finlandia-opacity-60;
    }

    @include active {
      background-color: $color-finlandia;
      transition: none;
    }

    &:disabled,
    &.is-disabled {
      opacity: 1;
      background-color: $color-finlandia-opacity-20;
      outline: 2px solid $color-finlandia-opacity-20;
      outline-offset: -2px;

      cursor: default;
      pointer-events: none;
    }
  }

  &--outlined-2 {
    position: relative;
    padding: 27px 124px 26px 126px;
    justify-content: center;

    letter-spacing: 0.1em;
    text-align: center;

    background-color: $color-finlandia-opacity-70;
    transition: background-color $trans-default;

    @include hover-focus {
      background-color: $color-finlandia-opacity-50;
    }

    @include active {
      background-color: $color-finlandia;
      transition: none;
    }

    &:disabled,
    &.is-disabled {
      background-color: $color-finlandia-opacity-20;

      cursor: default;
      pointer-events: none;
    }
  }

  &--full-width {
    width: 100%;
    letter-spacing: 0.15em;
  }

  &--with-icon {
    display: inline-flex;
    align-items: center;
    max-width: 50%;
    min-width: 350px;

    svg {
      flex-shrink: 0;
      margin-left: 35px;
    }
  }

  &--light {
    padding: 22px 14px 21px 15px;

    color: $color-finlandia;
    letter-spacing: 0.1em;

    background-color: transparent;
    border: 5px solid $color-finlandia;

    transition:
      background-color $trans-default,
      border-color $trans-default;

    &::before {
      display: none;
    }

    @include hover-focus {
      background-color: $color-finlandia-opacity-20;
      border-color: $color-finlandia;
    }

    @include active {
      background-color: $color-finlandia-opacity-50;
      border-color: $color-finlandia;
      transition: none;
    }

    &:disabled,
    &.is-disabled {
      background-color: transparent;
      border: 5px solid $color-finlandia-opacity-50;
    }
  }
}
