.catalogue {
  padding-bottom: 120px;

  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 93px;
  }

  &__title {
    position: relative;
    padding-top: 28px;
    padding-left: 8px;
    margin: 0;
    font-family: $ff-roboto;
    font-weight: 400;
    letter-spacing: 0.1em;
    color: $color-default-black;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 8px;
      display: block;
      width: 540px;
      height: 1px;
      background-color: $color-default-black;
    }
  }

  &__sorting {
    padding-top: 34px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 136px 155px;
    padding: 0;
    margin: 0 0 135px 0;
    list-style: none;

    @include vp-1699 {
      gap: 80px 15px;
    }

    @include vp-1023 {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__btn-wrap {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__show-more-btn {
    padding: 16px;
    margin-bottom: 30px;
    min-width: 500px;
    max-width: 1000px;
  }

  &__to-top-btn {
    position: absolute;
    right: 0;
  }

  &__no-items {
    margin: 350px auto 425px;
  }
}
