.item-card {
  position: relative;
  padding: 0 17px 11px 17px;
  width: 420px;
  min-height: 537px;
  background-color: $color-default-white;

  &__btn {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    margin: 0;
    font-size: 44px;
    cursor: pointer;
    border: none;
    background: transparent;
    z-index: 2;

    &:focus-visible {
      outline: 4px solid $color-finlandia-opacity-50;
      outline-offset: 3px;
    }
  }

  @include vp-1699 {
    width: 100%;
  }

  &__label {
    position: absolute;
    top: 0;
    left: 0;
    padding: 4px 14px 7px 19px;
    margin: 0;
    width: fit-content;
    max-width: 300px;
    font-family: $ff-roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
    color: $color-floral-white;
    background-color: $color-default-black;
    z-index: 1;

    &::before {
      content: "";
      position: absolute;
      bottom: 1px;
      left: 0;
      display: block;
      width: 19px;
      height: 16px;
      transform: translateY(100%);
      background-image: url("data:image/svg+xml,%3Csvg width='19' height='16' viewBox='0 0 19 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18.8679 0.223267L0 16V0.223267H18.8679Z' fill='black'/%3E%3C/svg%3E");
    }
  }

  &__img-wrap {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    height: 422px;
  }

  &__to-fav-btn {
    position: absolute;
    right: 76px;
    bottom: 3px;
  }

  &__desc-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }

  &__title {
    padding-top: 8px;
    padding-right: 8px;
    margin: 0;
    color: $color-default-black;
  }

  &__desc {
    margin: 0;
  }

  &__formatted-price {
    font-family: $ff-roboto;
    font-size: 32px;
    font-weight: 700;
    line-height: 38px;
    color: $color-default-black;
  }

  &__currency {
    padding-bottom: 4px;
    padding-left: 8px;
    font-family: $ff-roboto;
    font-size: 16px;
    font-weight: 300;
    line-height: 19px;
    color: $color-default-black;
    text-transform: uppercase;
  }

  &__price-wrap {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }

  &.is-favorite .item-card__to-fav-btn {
    & .button-heart__body {
      transition: fill $trans-default;
      transform: scale(1.02);
      transform-origin: center;
      fill: $color-default-black;
    }
  }
}
