.sorting-price {
  display: flex;

  &__title {
    margin: 0 38px 0 0;
    font-family: $ff-roboto;
    font-size: 48px;
    font-weight: 400;
    line-height: 56px;
    letter-spacing: 0.1em;
    color: $color-default-black;
  }

  &__link {
    display: flex;

    &--incr {
      margin-right: 27px;
    }

    &--active {
      svg {
        color: $color-default-black;
      }
    }

    @include hover-focus {
      & .sorting-price__icon {
        opacity: 0.6;
      }
    }

    &:focus-visible {
      outline: 4px solid $color-finlandia-opacity-50;
      outline-offset: 3px;
    }
  }

  &__icon {
    color: transparent;
    transition: opacity $trans-default, color $trans-default;
  }
}
