.btn-round {
  position: relative;
  width: 107px;
  height: 107px;

  background-color: transparent;
  color: $color-finlandia;
  border: 3px solid $color-finlandia;
  border-radius: 50%;

  cursor: pointer;
  transition: opacity $trans-ease-in-out;

  &::before {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;

    border-radius: 5px;

    content: "";
    z-index: -1;
  }

  & svg {
    fill: none;
    position: absolute;
    width: 80px;
    height: 85px;
  }

  &--to-left svg {
    left: 10px;
    top: 50%;
    transform: rotate(180deg) translate3d(0, 50%, 0);
  }

  &--to-right svg {
    top: 50%;
    right: 10px;
    transform: translate3d(0, -50%, 0);
  }

  &--to-top svg {
    left: 11px;
    top: 8px;
    transform: rotate(270deg);
  }

  &--to-bottom svg {
    left: 11px;
    top: 8px;
    transform: rotate(90deg);
  }

  &--size-small {
    width: 80px;
    height: 80px;

    & svg {
      width: 64px;
      height: 60px;
    }

    &.btn-round--to-left svg {
      left: 5px;
    }

    &.btn-round--to-right svg {
      right: 6px;
    }

    &.btn-round--to-top svg {
      left: 5px;
      top: 7px;
    }

    &.btn-round--to-bottom svg {
      left: 5px;
      top: 7px;
    }
  }

  @include hover-focus {
    outline: none;
    opacity: 0.7;
  }

  @include focus {
    &::before {
      outline: 4px solid $color-finlandia-opacity-50;
      outline-offset: 5px;
    }
  }

  @include active {
    opacity: 1;
    transition: none;

    & svg {
      fill: $color-finlandia;
    }
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
    cursor: default;

    & svg {
      fill: $color-finlandia;
    }
  }

  @include vp-1279 {
    width: 80px;
    height: 80px;

    & svg {
      width: 64px;
      height: 60px;
    }

    &--to-left svg {
      left: 5px;
    }

    &--to-right svg {
      right: 6px;
    }

    &--to-top svg {
      left: 5px;
      top: 7px;
    }

    &--to-bottom svg {
      left: 5px;
      top: 7px;
    }
  }
}
