.filter-color {
  padding-bottom: 140px;

  @include vp-1279 {
    padding-bottom: 40px;
  }

  &__title {
    position: relative;
    padding-top: 28px;
    padding-left: 8px;
    margin: 0 0 88px 0;
    font-family: $ff-roboto;
    font-weight: 400;
    letter-spacing: 0.1em;
    color: $color-default-black;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 8px;
      display: block;
      width: 540px;
      height: 1px;
      background-color: $color-default-black;
    }
  }

  &__form-fields {
    display: grid;
    grid-template-columns: repeat(6, 198px);
    gap: 50px 74px;
    padding-left: 26px;

    @include vp-1699 {
      grid-template-columns: repeat(6, 1fr);
      gap: 20px 10px;
      padding-left: 0;
    }

    @include vp-1279 {
      display: flex;
      gap: 0;
      flex-wrap: wrap;
      margin-left: -40px;
    }
  }

  &__form-field {
    @include vp-1279 {
      margin-bottom: 40px;
      margin-left: 40px;
    }
  }
}
