.advantages-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  margin: 0;
  padding: 0;
  max-width: 1368px;

  list-style: none;

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 500px;
    max-width: 501px;
    padding: 70px 0 90px 0;

    text-align: center;

    background: $color-default-white-opacity;
    border-radius: 30px;

    @media (max-width: 1769px) {
      min-width: 420px;
      margin-right: 20px;
    }

    @media (max-width: 1540px) {
      min-width: 370px;
      padding: 30px 0;
    }

    @include vp-1439 {
      min-width: 300px;
    }

    @include vp-1279 {
      min-width: 250px;
    }

    svg {
      margin-bottom: 10px;

      @include vp-1439 {
        width: 80px;
      }
    }
  }

  &__item:last-child {
    min-width: 481px;
    padding-left: 15px;

    @media (max-width: 1769px) {
      min-width: 420px;
      padding-left: 0;
    }

    @media (max-width: 1540px) {
      min-width: 370px;
    }

    @include vp-1439 {
      min-width: 300px;
    }

    @include vp-1279 {
      min-width: 250px;
    }
  }

  p {
    margin: 0 0 63px 0;

    font-weight: 400;
    font-size: 56px;
    line-height: 94px;
    color: $color-default-black;

    @media (max-width: 1769px) {
      font-size: 45px;
    }

    @media (max-width: 1540px) {
      font-size: 40px;
      margin-bottom: 20px;
    }

    @include vp-1439 {
      font-size: 30px;
    }

    @include vp-1279 {
      font-size: 25px;
    }
  }
}
