.filter-field-text {
  position: relative;

  &__input {
    position: absolute;
    padding: 0;
    margin: -1px;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    white-space: nowrap;
    border: 0;
    clip-path: inset(100%);

    &:checked + .filter-field-text__label {
      .filter-field-text__text {
        font-weight: 700;
        color: $color-finlandia;
        transform: scale(1.22);

        @include vp-1023 {
          transform: scale(1.1);
        }

        &::before {
          content: "";
          position: absolute;
          right: 0;
          bottom: -9px;
          left: 0;
          display: block;
          height: 4px;
          transition: opacity $trans-default;
          background-color: $color-finlandia;
        }
      }
    }

    &:disabled:not(:checked) + .filter-field-text__label {
      pointer-events: none;

      .filter-field-text__text {
        color: $color-default-black-opacity-50;
      }
    }
  }

  &__label {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__text {
    position: relative;
    font-family: $ff-roboto;
    font-size: 36px;
    font-weight: 400;
    line-height: 42px;
    color: $color-default-black;
    transition: transform $trans-default, color $trans-default;
    text-align: center;
    will-change: transform;

    @include vp-1699 {
      font-size: 26px;
      line-height: 30px;
    }
  }
}
