.advantages {
  position: relative;

  margin-bottom: 105px;
  padding-bottom: 20px;

  &__wrapper {
    padding-left: 8px;
    padding-top: 28px;
  }

  &__img {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;

    display: flex;
    justify-content: flex-end;

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  &__content {
    z-index: 10;

    padding-top: 116px;
    padding-left: 10px;
  }

  h2 {
    position: relative;
    margin: 0 0 96px 0;

    &::after {
      content: "";
      position: absolute;
      width: 540px;
      height: 1px;
      background-color: $color-default-black;
      top: -28px;
      left: 0;
    }
  }
}
