.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 600px;
    padding: 40px;

    background-color: $color-default-white;

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &--product {
    background-color: rgba($color-default-black, 0.7);

    & .modal__wrapper {
      padding: 100px 103px;

      @include vp-1439 {
        padding: 100px 60px;
      }

      @include vp-1279 {
        padding: 100px 40px;
      }
    }

    & .modal__content {
      width: 100%;
      max-width: 1564px;
      padding: 0;
    }
  }

  &--error {
    background-color: transparent;

    & .modal__wrapper {
      padding: 100px;

      @include vp-1439 {
        padding: 100px 60px;
      }

      @include vp-1279 {
        padding: 100px 40px;
      }
    }

    & .modal__content {
      width: max-content;

      background-color: transparent;
    }
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }

  &--deferred {
    background-color: transparent;
    top: 238px;
    position: fixed;

    .modal__wrapper {
      padding: 0;
      padding-bottom: 160px;
      //padding: 0 102px 160px 102px;
    }

    .modal__content {
      padding: 0;
      width: 100%;
    }
  }
}
