.header-count {
  position: relative;

  display: inline-flex;

  svg {
    transform: translateZ(0);
  }

  &__btn {
    position: absolute;
    top: 7px;
    right: 95px;

    padding: 15px 0 0;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;

    transition: $trans-default;

    @include hover {
      opacity: 0.5;
    }

    @include active {
      opacity: 1;
    }

    @include focus {
      svg {
        outline: 4px solid $color-finlandia-opacity-50;
        outline-offset: 3px;
      }
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    z-index: 10;
  }

  &__text {
    margin-bottom: 35px;
    margin-right: 30px;
  }

  &__price {
    span {
      margin-left: 5px;
      text-transform: uppercase;
    }
  }

  &__count {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: flex-end;
    word-break: break-all;
    min-width: 30px;
    padding: 5px 10px;
    text-align: center;

    height: 30px;
    margin-right: 30px;

    background-color: $color-candy;
    border-radius: 50%;

    z-index: 10;
  }
}
