.image-slide {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.image-slide img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.image-slide__author {
  position: absolute;
  bottom: 77px;
  right: 161px;
  letter-spacing: 0.02em;
}
