.popup-deferred {
  flex-grow: 1;
  margin-bottom: 226px;

  &__wrapper {
    padding: 0 102px;

    background-color: $color-ivory;
  }

  &__catalog {
    margin: 0 0 140px 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__item {
    margin-bottom: 190px;
  }

  &__item:last-of-type {
    margin-bottom: 0;
  }

  .title {
    @include vp-1439 {
      font-size: 50px;
    }

    @include vp-1279 {
      font-size: 40px;
    }

    @media (max-width: 1720px) {
      font-size: 70px;
    }
  }

  &__container {
    padding: 174px 30px 155px;
  }

  &__btn {
    margin-bottom: 127px;
  }

  &__btn-container {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 223px;

    svg {
      flex-shrink: 0;
      margin-left: 29px;
    }
  }

  &__sum {
    position: relative;

    display: grid;
    width: 100%;
    padding: 37px 13px 50px 34px;

    column-gap: 100px;

    background-color: $color-default-white;

    grid-template-columns: 1fr 1fr;

    @include vp-1439 {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &::after {
      content: "";
      position: absolute;
      top: -59px;
      left: 17px;

      width: 540px;
      height: 1px;

      background-color: $color-default-black;
    }

    .text {
      @include vp-1439 {
        width: 100%;
        margin-bottom: 40px;

        text-align: center;
      }
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 100px;

    @media (max-width: 1769px) {
      margin-right: 20px;
      margin-bottom: 40px;
    }

    .text {
      margin: 0 0 79px 0;

      letter-spacing: 6px;

      @media (max-width: 1769px) {
        margin-bottom: 20px;
      }
    }
  }

  &__block:last-child {
    margin-right: 0;

    @media (max-width: 1769px) {
      margin-bottom: 0;
    }
  }

  &__block-wrap {
    display: flex;
    flex-direction: row;

    justify-self: flex-end;
  }

  &__count {
    margin-left: 18px;

    font-weight: 400;
    font-size: 48px;
    line-height: 56px;
    color: $color-default-black;
  }
}
