.deferred-card {
  width: 100%;
  background-color: $color-default-white;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  min-height: 446px;
  position: relative;
  padding: 0 29px 10px 0;

  box-shadow: 0 0 20px 0 $color-candy;

  @media (max-width: 1500px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  &__img {
    min-width: 370px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-right: 60px;
    align-items: center;
    padding-left: 10px;

    @media (max-width: 1760px) {
      margin-right: 10px;
    }

    @media (max-width: 1400px) {
      min-width: 200px;

      img {
        width: 200px;
      }
    }
  }

  .title {
    margin-bottom: 102px;

    @include vp-1279 {
      font-size: 45px;
      max-width: 300px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    padding-top: 45px;
    padding-bottom: 20px;
    margin-right: 33px;

    .text {
      max-width: 590px;
    }

    @media (max-width: 1760px) {
      margin-right: 30px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 44px;
    right: 10px;
    width: 44px;
    height: 46px;
    background-color: $color-default-white;

    svg {
      color: $color-default-black;
      width: 44px;
      height: 46px;
    }
  }

  &__loader {
    display: none;
    animation-name: rotation;
    animation-duration: 3s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;

    @keyframes rotation {
      0% {
        transform: rotate(0deg);
      }

      100% {
        transform: rotate(360deg);
      }
    }
  }

  &__count {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding-top: 65px;
    margin-right: 122px;

    span {
      font-weight: 500;
      font-size: 48px;
      line-height: 56px;
      color: $color-default-black;
      margin: 0 30px;
    }

    @media (max-width: 1760px) {
      margin-right: 60px;
    }
  }

  &__price {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 65px;
  }
}

.deferred-card.is-loading {
  .deferred-card__close-btn {
    display: none;
  }

  .deferred-card__loader {
    display: block;
  }
}
