.modal-product {
  position: relative;

  display: flex;
  flex-direction: column;
  padding: 324px 115px 198px;
  background-color: $color-default-white;
  width: 100%;

  @include vp-1699 {
    padding: 270px 50px 150px;
  }
}

.modal-product__btn-close {
  position: absolute;
  right: 95px;
  top: 71px;
}

.modal-product__loader {
  display: none;
  position: absolute;
  right: 95px;
  top: 71px;
  animation-name: rotation;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;

  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }
}

.modal-product__slider {
  margin-bottom: 20px;
}


.is-loading {
  .modal-product__btn-close {
    display: none;
  }

  .modal-product__loader {
    display: block;
  }
}
