.footer {
  padding: 47px 0;

  background-color: $color-default-white;

  &__wrapper {
    display: grid;

    grid-template-columns: 30% 1fr 30%;

    @include vp-1279 {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  &__logo-content {
    display: flex;
    align-items: center;
    justify-self: flex-start;
    align-self: center;
    padding-left: 15px;

    @include vp-1279 {
      flex-direction: column;
      align-items: flex-start;
    }

    p {
      margin: 0;

      font-weight: 400;
      font-size: 20px;
      line-height: 23px;
      color: $color-default-black;
      letter-spacing: 0.5px;
    }
  }

  h2 {
    margin: 0 0 4px 0;

    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
    color: $color-default-black;
    text-transform: uppercase;
    letter-spacing: 5px;
  }

  &__text {
    padding-left: 10px;

    @include vp-1279 {
      padding-left: 0;
    }
  }

  &__social {
    justify-self: center;
    align-self: center;
    padding: 5px 0 0 30px;
  }

  &__develope {
    padding-top: 5px;

    justify-self: flex-end;
    align-self: center;

    p {
      max-width: 178px;
      margin: 0;
      text-align: end;
    }
  }
}
